



















export default {
	data() {
		return {
			positionTop: 0,
			showClearIcon: false,
			inputClearValue: "",
			phone: "",
		};
	},
	methods: {
		goCaptcha() {
			var _this = this
			if(this.phone.length == 11) {
				uni.request({
					url: 'http://www.aikm.cn/api/auth',
					method: 'POST',
					data: {phone: _this.phone},
					success: res => {
						console.log(JSON.stringify(res.data))
						if(res.data.code == 1) {
							uni.showToast({
								title: '验证码已发送',
								duration: 2000,
								icon: 'none',
							});
							// return
							setTimeout(function() {
								uni.navigateTo({
									url: '../captcha/captcha?phone=' + _this.phone
								});
							}, 800)
						} else {
							uni.showToast({
								title: res.data.msg,
								duration: 2000,
								icon: 'none',
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			} else {
				uni.showToast({
					title: '请输入正确手机号',
					duration: 2000,
					icon: 'none',
				});
			}
		},
		initPosition() {
			/**
			* 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			* 反向使用 top 进行定位，可以避免此问题。
			*/
			this.positionTop = uni.getSystemInfoSync().windowHeight - 140;
		},
		bindClearInput: function (e) {
			this.inputClearValue = e.target.value;
			if (e.target.value.length > 0) {
				this.showClearIcon = true;
			} else {
				this.showClearIcon = false;
			}
		},
		clearIcon: function () {
			this.inputClearValue = "";
			this.showClearIcon = false;
		},
		goWechatLogin() {
			uni.navigateTo({
				url: '../../login/login-wechat/login-wechat'
			})
		},
	},
	onLoad() {
		this.initPosition();
		
	}
}



